.App {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 120vh;
  background : linear-gradient(90deg, white, rgb(149, 160, 232));
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size : 50px;
  margin-bottom: 100px;
}

.board{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  width: 80vw;
  height: 90vh;
  max-width: 750px;
}

.card{
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100%;
  border: 1px solid;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gameover-board, .gamestart-board{
  text-align: center;
  margin: 0 auto;/* 가운데 정렬 */
}

.start-btn{
  background-color: #007bff;
  color: #fff; 
  font-size: 16px; 
  padding: 10px 20px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer; 
  transition: background-color 0.3s ease;
}

.start-btn:hover {
  background-color: #0056b3;
}


/* position 정리 
  static : 기본값 left top right bottom 안먹음
  relative : static에서 이동
  absolute : 담겨진 가장 가까운 박스에서 이동 
  fixed : windows안 이동
  sticky : 스크롤 따라 이동 

display 정리
  inline : 자기 크기만큼만
  inline-block : 자기크기만큼만 넓이 길이 적용
  block : 한줄 다

  display: flex;
  flex-direction: row - reverse (reverse는 뒤에서 부터 12345하면 54321)
  flex-wrap : nowrap : 화면이 줄면 안에 컴포넌트도 같이 줄어듦
              wrap : 화면이 줄면 안에 컴포넌트들이 줄바꿈
  flex-flow : (direction) (wrap)
  jusstify-content :  flex-start
                      flex-end (12345하면 12345부터 뒤로)
                      center
                      space-around : 일정한 간격
                      space-between : 일정간격 맨 오왼 딱 맞게
  align-items : baseline : 글자의 수직위치가 맞게
                center : 수직 가운데 정렬
  align-content : space - between : 수직일정간격 맨 오외 딱 맞게
*item부분
flex-grow : 1~ :  윈도우 창에 따라 크기변함
flex-shrink : 1 ~: 줄어드는 정도
flex-basis : 60% : 60%차지함
align-self : center*/